<template>
<div>
  <div>
    <el-container>
      <el-header style="align-items: center;margin-top:20px;">
        <div class="crumbs">
          <el-breadcrumb separator="/">
            <el-breadcrumb-item><i class="el-icon-menu"></i></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
          </el-breadcrumb>
        </div>
      </el-header>
      <el-main style="padding-left: 0px;padding-top: 0px" v-loading="upadte_tech_loading">
        <div class="table">
            <el-card class="box-card">
                <div slot="header" class="clearfix">
                    <span></span>
                </div>
                <div>
                    <el-form ref="ruleform" :model="ruleform" :rules="rules" label-width="120px">
                      <el-form-item label=""  prop="piWenDaiMa">
                          <el-input v-model="ruleform.piWenDaiMa" placeholder="" style="width:30%" :disabled="!toupdate"></el-input>
                      </el-form-item>
                      <el-form-item label=""  prop="nanDuXiShu">
                          <el-input v-model="ruleform.nanDuXiShu" placeholder="" style="width:30%" :disabled="!toupdate"></el-input>
                      </el-form-item>
                      <el-form-item label=""  prop="tieHuaBuShu">
                        <el-input v-model="ruleform.tieHuaBuShu" placeholder="" style="width:30%" :disabled="!toupdate"></el-input>
                      </el-form-item>
                      <el-form-item label=""  prop="gongYiCanShu">
                          <el-input v-model="ruleform.gongYiCanShu" type="textarea" placeholder="" style="width:50%;" :disabled="!toupdate"></el-input>
                      </el-form-item>
                      <div style="height:1px;width:100%;border-bottom: 1px solid #e6ebf5;
                      box-sizing: border-box;margin-bottom:15px;">
                      </div>
                        <el-form-item label="" prop="guangZhe">
                            <el-input v-model="ruleform.guangZhe" placeholder="" style="width:30%"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="chaiLiao">
                            <el-input v-model="ruleform.chaiLiao" placeholder="" style="width:30%"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="tiaoZheng">
                            <el-input v-model="ruleform.tiaoZheng" placeholder="" type="textarea" style="width:50%;"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="remark">
                            <el-input v-model="ruleform.remark" type="textarea" style="width:50%;"></el-input>
                        </el-form-item>
                        <div style="height:1px;width:100%;border-bottom: 1px solid #e6ebf5;
                        box-sizing: border-box;margin-bottom:15px;">
                        </div>
                        <div class="handle-box">
                            <el-button type="primary" icon="plus" class="mr10" style="float:right" @click="addLanMo"><i class="el-icon-plus"></i></el-button>
                        </div>
                        <el-table :data="tableData" border style="width: 100%">
                            <el-table-column label=""  type="index"  width="50">
                            </el-table-column>
                            <el-table-column prop="beginTime" label="">
                            </el-table-column>
                            <el-table-column prop="lanMoTime" label="">
                            </el-table-column>
                            <el-table-column prop="shenDu" label="">
                            </el-table-column>
                            <el-table-column prop="yaoShui" label="">
                            </el-table-column>
                            <el-table-column prop="remark" label="">
                            </el-table-column>
                            <el-table-column label="">
                                <template slot-scope="scope">
                          <el-button size="mini" type="primary" @click="toEdit(scope.row)"></el-button>
                          <el-button size="mini" type="danger" @click="delMouldRecord(scope.row)"></el-button>
                        </template>
                            </el-table-column>
                        </el-table>
                    </el-form>
                </div>
                <div style="height:1px;width:100%;border-bottom: 1px solid #e6ebf5;
                  box-sizing: border-box;margin-bottom:15px;margin-top:15px;">
                </div>
                <div class="card-bottom">
                    <el-button @click="toParent"></el-button>
                    <el-button style="float:right" type="primary" @click="buChong('ruleform')"></el-button>
                </div>
            </el-card>
            <!--  -->
            <div v-loading="addlanmo_loading">
            <el-dialog title="" :visible.sync="dialogFormVisible" width='40%'>
                <div class="form-box" >
                    <el-form ref="form" :model="form" :rules="rules" label-width="80px">
                        <el-form-item label="" prop="beginTime">
                            <el-date-picker v-model="form.beginTime" style="width:50%" format="yyyy  MM  dd  HH:mm:ss" value-format="yyyy-MM-dd HH:mm:ss" type="datetime" placeholder="">
                            </el-date-picker>
                        </el-form-item>
                        <el-form-item label="" prop="lanMoTime">
                          <el-date-picker v-model="form.lanMoTime" style="width:50%" format="yyyy  MM  dd  HH:mm:ss" value-format="yyyy-MM-dd HH:mm:ss" type="datetime" placeholder="">
                          </el-date-picker>
                        </el-form-item>
                        <el-form-item label="" prop="shenDu">
                            <el-input placeholder="" v-model="form.shenDu" style="width:50%"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="yaoShui">
                            <el-input placeholder="" v-model="form.yaoShui" style="width:50%"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="remark">
                            <el-input type="textarea" v-model="form.remark"></el-input>
                        </el-form-item>
                    </el-form>
                </div>
                <div slot="footer" class="dialog-footer">
                    <el-button type="primary"v-show="!this.updatDisable" @click="submitForm('form')"> </el-button>
                    <el-button type="primary" v-show="this.updatDisable" @click="updateForm('form')"> </el-button>
                    <el-button @click="quit"> </el-button>
                </div>
            </el-dialog>
          </div>
        </div>
      </el-main>
    </el-container>
  </div>
</div>
</template>
<script>
export default {
  props: {
    techCards:{
      type:Object
    }
  },
  data() {
    return {
      toupdate: false,
      upadte_tech_loading:false,
      addlanmo_loading:false,
      dialogFormVisible: false,
      item_techId:'',
      uid:'',
      name:'',
      username:'',
      updatDisable: false,
      ruleform: {},
      form:{},
      tableData: [],
      user_role: [],
      rules: {
        productName: [{
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            min: 0,
            max: 255,
            message: ' 0  255 ',
            trigger: 'blur'
          }
        ],
        wenlidaima: [{
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            min: 0,
            max: 255,
            message: ' 0  255 ',
            trigger: 'blur'
          }
        ],
        carType: [{
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            min: 1,
            max: 255,
            message: ' 1  255 ',
            trigger: 'blur'
          }
        ],
        clientCompanyName: [{
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            min: 1,
            max: 255,
            message: ' 1  255 ',
            trigger: 'blur'
          }
        ],
        contactPerson: [{
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            min: 1,
            max: 255,
            message: ' 1  255 ',
            trigger: 'blur'
          }
        ],
        contactPhone: [{
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            min: 1,
            max: 255,
            message: ' 1  255 ',
            trigger: 'blur'
          }
        ],
        paytype: [{
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            min: 1,
            max: 255,
            message: ' 1  255 ',
            trigger: 'blur'
          }
        ],
        deliverytime: [{
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            min: 1,
            max: 255,
            message: ' 1  255 ',
            trigger: 'blur'
          }
        ],
        remark: [{
          min: 0,
          max: 2048,
          message: ' 0  2048 ',
          trigger: 'blur'
        }]
      },
    }
  },
  methods: {
    buChong(formName){
      let self = this;
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.upadte_tech_loading = true;
          this.postRequest("/tech/added/update/techcard", this.ruleform).then(resp => {
            this.upadte_tech_loading = false;
            if (resp && resp.status == 200 && resp.data.code == 0) {
              self.$message.success('');
              // this.techCards = {};
              this.toParent();
            }else{
              self.$message.error('');
            }
          })
        } else {
          self.$message.error('');
          return false;
        }
      });
    },
    addLanMo(){
      let self = this;
      self.form = {};
      self.dialogFormVisible = true;
    },
    updateForm(formName){
      let self = this;
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.addlanmo_loading = true;
          this.postRequest("/tech/added/update/lanmo", this.form).then(resp => {
            this.addlanmo_loading = false;
            if (resp && resp.status == 200 && resp.data.code == 0) {
              self.$message.success('');
              this.dialogFormVisible = false;
              this.resetForm('form');
              this.updatDisable = false;
              this.getLanMoList();
            }else{
              self.$message.error('');
            }
          })
        } else {
          self.$message.error('');
          return false;
        }
      });
    },
    quit(){
      this.resetForm('form');
      this.dialogFormVisible = false;
      this.updatDisable = false;
    },
    toEdit(item){
      let self = this;
      self.updatDisable = true;
      self.form = item;
      this.dialogFormVisible = true;
    },
    toGetInfo(orderId){
      this.getRequest("/tech/added/get/tech?orderId="+orderId).then(resp => {
        if (resp && resp.status == 200 && resp.data.code == 0) {
          this.ruleform = resp.data.data.techcard;
          this.item_techId = resp.data.data.techcard.id;
          this.getLanMoList();
        }
      })
    },
    toParent(){
      this.$emit("listenToChild",1)
    },
    // 
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    // 
    submitForm(formName) {
      let self = this;
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.addlanmo_loading = true;
          self.form.addUserId = this.uid;
          self.form.addUserName = this.name;
          if(this.item_techId =='' || this.item_techId == 'undefinded'){
            self.$message.error('');
            return;
          }
          self.form.techId = this.item_techId;
          this.postRequest("/tech/added/add/lanmo", this.form).then(resp => {
            this.addlanmo_loading = false;
            if (resp && resp.status == 200 && resp.data.code == 0) {
              this.dialogFormVisible = false;
              self.resetForm('form');
              this.getLanMoList();
            }
          })
        } else {
          self.$message.error('');
          return false;
        }
      });
    },
    getLanMoList(){
      this.getRequest("/tech/added/lanmo/list?techId="+this.item_techId).then(resp => {
        if (resp && resp.status == 200 && resp.data.code == 0) {
           this.tableData = resp.data.data.lanmolist;
        }
      })

    },
    deletedLanMo(item){
      let self = this;
      this.getRequest("/tech/added/del/lanmo?id="+item.id).then(resp => {
        if (resp && resp.status == 200 && resp.data.code == 0) {
            self.$message.success("")
            this.getLanMoList();
        }else {
          self.$message.success("")
        }
      })
    },
    checkRole(){
      let user = localStorage.getItem('user');
      let userinfo = JSON.parse(user);
      this.user_role = userinfo.roles;
      if(this.user_role != 'undefinded' && this.user_role.length > 0){
        for (var i = 0; i < this.user_role.length; i++) {
           if(this.user_role[i].name==="ROLE_admin" || this.user_role[i].name==="ROLE_jishu"){
             this.toupdate = true;
             return;
           }
        }
      }
    },
    delMouldRecord(lanmo) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.deletedLanMo(lanmo);
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    }
  },
  mounted: function() {
    this.$nextTick(function() {
    })
    this.username = localStorage.getItem('cp_username');
    this.uid = localStorage.getItem('cp_uid');
    this.name = localStorage.getItem('cp_name');
    this.checkRole();

  },
  watch: {
    techCards: function(val, oldVal){
      this.ruleform = val;
      this.toGetInfo(val.id);
    }
  }
}
</script>



