<template>
<div>
  <div>
    <div class="crumbs">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item><i class="el-icon-share"></i></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-container>
      <el-main style="padding-left: 0px;padding-top: 0px">
        <!--  -->
        <div>
          <el-card class="box-card" style="margin-top:25px;">
            <div slot="header" class="clearfix">
              <span></span>
            </div>
            <div>
              <el-row :gutter="20">
                <el-col :span="6">
                  <div @click="addbuzhou">
                    <el-card style="min-height:121px;" class="mould-card">
                      <div style="padding: 14px;">
                        <img class="image">
                        <i class="el-icon-plus"></i>
                      </div>
                    </el-card>
                  </div>
                </el-col>
                <el-col :span="6" v-for=" step in steps">
                  <el-card :body-style="{ padding: '0px' }" class="mould-card">
                    <div style="padding: 14px;">
                      <h4>{{step.stepName}}</h4>
                      <h5>:{{step.addUserName}}</h5>
                      <div v-show="step.isCheck === false">
                        <h6></h6></div>
                      <div v-show="step.isCheck === true">
                        <h6></h6></div>
                      <div class="bottom clearfix">
                        <p class="time">{{step.remark}}</p>
                        <el-dropdown style="float: right;color: #409EFF;">
                          <span class="el-dropdown-link"><i class="el-icon-edit"></i>
                                            </span>
                          <el-dropdown-menu slot="dropdown">
                            <el-dropdown-item>
                              <el-button type="text" @click="del(step.id)"> </el-button>
                            </el-dropdown-item>
                          </el-dropdown-menu>
                        </el-dropdown>
                      </div>
                    </div>
                  </el-card>
                </el-col>
              </el-row>
            </div>
          </el-card>
        </div>
      </el-main>
    </el-container>
  </div>
  <el-dialog title="" v-loading="checkLoading" :show-close="false" :close-on-click-modal="false" :close-on-press-escape="false" :visible.sync="dialogFormVisible" width='50%'>
    <div class="form-box">
      <el-form ref="form" :model="form" :rules="rules" label-width="80px">
        <el-form-item label="" prop="stepName">
          <el-input v-model="form.stepName" placeholder="" style="width:30%"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-switch v-model="form.isCheck"></el-switch>
        </el-form-item>
        <el-form-item label="">
          <el-switch v-model="form.isBusRemark"></el-switch>
        </el-form-item>
        <el-form-item label="">
          <el-input type="textarea" v-model="form.remark" style="width:50%"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button style="float:right" type="primary" @click="submitForm('form')"> </el-button>
        <el-button @click="dialogFormVisible = false"> </el-button>
      </div>
    </div>
  </el-dialog>
</div>
</template>
<script>
export default {
  props: {
    title: String
  },
  data() {
    return {
      dialogFormVisible: false,
      checkLoading: false,
      steps: [],
      username: '',
      name: '',
      uid: '',
      form: {
        isCheck: true,
        isBusRemark: false
      },

      rules: {
        stepName: [{
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            min: 1,
            max: 255,
            message: ' 0  255 ',
            trigger: 'blur'
          }
        ]
      },
    }
  },
  methods: {
    // 
    submitForm(formName) {
      let self = this
      this.$refs[formName].validate((valid) => {
        if (valid) {
          self.checkLoading = true;
          self.form.addUserId = this.uid;
          self.form.addUserName = this.name;
          this.postRequest("/work/step/add", self.form).then(resp => {
            self.checkLoading = false;
            if (resp && resp.status == 200 && resp.data.code == 0) {
              self.$message.success("");
              self.dialogFormVisible = false;
              self.loadAllSteps();
            } else {
              self.$message.error('');
            }
          })
        } else {
          return false;
        }
      });
    },
    addbuzhou() {
      this.dialogFormVisible = true;
    },
    loadAllSteps() {
      let self = this
      this.getRequest("/work/step/list?").then(resp => {
        if (resp && resp.status == 200 && resp.data.code == 0) {
          self.steps = resp.data.data.steplist
        } else {
          console.log("");
        }
      });
    },
    del(id) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.todel(id);
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    },
    todel(id) {
      let self = this
      this.getRequest("/work/step/del?id=" + id).then(resp => {
        if (resp && resp.status == 200 && resp.data.code == 0) {
          self.$message.success("")
          self.loadAllSteps();
        } else {
          self.$message.error("")
        }
      });
    },
    // 
    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  },
  mounted: function() {
    this.username = localStorage.getItem('cp_username');
    this.uid = localStorage.getItem('cp_uid');
    this.name = localStorage.getItem('cp_name');
    this.loadAllSteps();
  }
}
</script>



